  border-radius: 6px;
  background: rgba(148, 163, 184, 0.25);
  transition: background 120ms ease;
  margin: 0 4px;
  align-self: stretch;
}

.expression-inner-splitter:hover,
.expression-inner-splitter:focus-visible,
.expression-inner-splitter-dragging {
  background: rgba(59, 130, 246, 0.5);
}

.expression-tab {
  border: none;
  border-radius: 12px;
  padding: 10px 16px;
  background: transparent;
  color: rgba(148, 163, 184, 0.82);
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  cursor: pointer;
  transition: background 120ms ease, color 120ms ease;
  text-align: left;
  width: 100%;
}

.expression-tab-file {
  font-weight: 500;
  color: rgba(226, 232, 240, 0.8);
}

.expression-tab-main {
  color: #38bdf8;
}

.expression-tab-view {
  color: #f97316;
}

.expression-tab-return {
  color: #34d399;
}

.expression-tab-return-arrow {
  font-size: 0.85rem;
  font-weight: 700;
  letter-spacing: 0.08em;
}

.expression-tab-return-label {
  font-size: 0.9rem;
  letter-spacing: 0.08em;
}

.expression-tab-add {
  min-width: 0;
  width: 100%;
}

.expression-tabs-shell:hover .expression-root-menu-wrapper,
.expression-tabs-shell:focus-within .expression-root-menu-wrapper,
.expression-folder-header:hover .expression-folder-menu-wrapper,
.expression-folder-header:focus-within .expression-folder-menu-wrapper {
  opacity: 1;
  pointer-events: auto;
}

.expression-tab-row:hover .expression-tab-menu-wrapper,
.expression-tab-row:focus-within .expression-tab-menu-wrapper {
  opacity: 1;
  pointer-events: auto;
}

.expression-tab-row {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.expression-tab-row-nested {
  padding-left: 20px;
}

.expression-tab-main {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
}

.expression-tab-icons {
  display: flex;
  gap: 4px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 120ms ease;
}

.expression-tab-row:hover .expression-tab-icons,
.expression-tab-row:focus-within .expression-tab-icons,
.expression-tab-main:hover .expression-tab-icons,
.expression-tab-main:focus-within .expression-tab-icons {
  opacity: 1;
  pointer-events: auto;
}

.expression-rename-row .expression-tab-icons,
.expression-folder-header-edit .expression-tab-icons {
  opacity: 1;
  pointer-events: auto;
}

.expression-tab-main > .expression-tab {
  flex: 1;
}

.expression-tab-icon {
  display: inline-flex;
  margin-right: 6px;
}

.expression-tab-label {
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

.expression-menu-wrapper {
  position: relative;
  display: inline-flex;
  opacity: 0;
  pointer-events: none;
  transition: opacity 120ms ease;
}

.expression-menu-wrapper-static {
  opacity: 1;
  pointer-events: auto;
}

.expression-menu-wrapper-open {
  opacity: 1;
  pointer-events: auto;
}

.expression-menu-button {
  border: none;
  background: rgba(226, 232, 240, 0.3);
  color: rgba(15, 23, 42, 0.75);
  border-radius: 999px;
  padding: 4px;
  width: 32px;
  height: 32px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.15);
  transition: background 120ms ease, color 120ms ease, box-shadow 120ms ease;
}

.expression-menu-button:hover {
  background: rgba(226, 232, 240, 0.6);
  color: rgba(15, 23, 42, 0.85);
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.2);
}

.expression-menu-button:focus-visible {
  outline: 2px solid #38bdf8;
  outline-offset: 2px;
}

.expression-menu {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  display: flex;
  flex-direction: column;
  background: #fff;
  color: #0f172a;
  border-radius: 12px;
  min-width: 150px;
  box-shadow: 0 20px 45px rgba(15, 23, 42, 0.35);
  padding: 4px;
  z-index: 5;
}

.expression-menu-portal {
  position: fixed;
  right: auto;
}

.expression-menu-item {
  border: none;
  background: transparent;
  padding: 8px 12px;
  text-align: left;
  border-radius: 8px;
  font-weight: 600;
  color: inherit;
  cursor: pointer;
}

.expression-menu-item:hover,
.expression-menu-item:focus-visible {
  background: rgba(15, 23, 42, 0.08);
}

.expression-root-menu-wrapper {
  align-self: flex-start;
}

.expression-folder-menu-wrapper,
.expression-tab-menu-wrapper {
  margin-left: auto;
}

.expression-icon-button {
  border: none;
  background: transparent;
  color: rgba(148, 163, 184, 0.7);
  border-radius: 8px;
  padding: 6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 120ms ease, color 120ms ease;
}

.expression-icon-button:hover {
  color: #f8fafc;
  background: rgba(59, 130, 246, 0.25);
}

.expression-icon-button:focus-visible {
  outline: 2px solid #38bdf8;
  outline-offset: 2px;
}

.expression-icon-button svg {
  display: block;
}

.expression-rename-row {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
}

.expression-rename-input {
  flex: 1;
}

.expression-rename-error {
  margin: 0;
}

.expression-folder {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.expression-folder-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  padding: 0 4px;
}

.expression-folder-header > .expression-folder-name {
  flex: 1;
}

.expression-folder-toggle {
  border: none;
  background: rgba(226, 232, 240, 0.18);
  color: rgba(226, 232, 240, 0.85);
  border-radius: 999px;
  width: 28px;
  height: 28px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 120ms ease, color 120ms ease;
}

.expression-folder-toggle:hover,
.expression-folder-toggle:focus-visible {
  background: rgba(226, 232, 240, 0.32);
  color: #fff;
}

.expression-folder-collapsed .expression-folder-tabs {
  display: none;
}

.expression-folder-header-edit {
  flex-direction: column;
  align-items: stretch;
}

.expression-folder-name {
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba(226, 232, 240, 0.86);
}

.expression-folder-tabs {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px;
  margin-left: 8px;
  border-left: 1px solid rgba(148, 163, 184, 0.25);
  border-radius: 12px;
  background: rgba(15, 23, 42, 0.35);
}

.expression-tree-toolbar {
  margin-bottom: 12px;
  display: flex;
  gap: 8px;
}

.expression-toolbar-button {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  border: 1px solid rgba(148, 163, 184, 0.35);
  background: rgba(59, 130, 246, 0.18);
  color: #f8fafc;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 140ms ease, transform 140ms ease, color 140ms ease;
  box-shadow: 0 1px 3px rgba(15, 23, 42, 0.35);
}

.expression-toolbar-button:hover,
.expression-toolbar-button:focus-visible {
  background: rgba(59, 130, 246, 0.35);
  color: #fff;
  transform: translateY(-1px);
}

.expression-toolbar-button svg {
  width: 18px;
  height: 18px;
}

.expression-root-input {
  width: 100%;
}

.expression-tab-nested {
  padding-left: 20px;
}

.expression-folder-input {
  width: 100%;
}

.expression-tab:focus-visible {
  outline: 2px solid #38bdf8;
  outline-offset: 2px;
}

.expression-tab-active {
  background: rgba(59, 130, 246, 0.25);
  color: #f8fafc;
}

.expression-tab-error-state {
  box-shadow: inset 0 0 0 1px rgba(248, 113, 113, 0.6);
  color: rgba(248, 113, 113, 0.85);
}

.expression-tab-error-state.expression-tab-active {
  background: rgba(248, 113, 113, 0.18);
  color: #fee2e2;
}

.expression-tab-input {
  border: 1px solid rgba(148, 163, 184, 0.35);
  border-radius: 999px;
  padding: 8px 16px;
  background: rgba(15, 23, 42, 0.8);
  color: #e2e8f0;
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  width: 100%;
}

.expression-tab-input:focus {
  outline: 2px solid #38bdf8;
  outline-offset: 2px;
}

.expression-tab-error {
  margin: 0;
  font-size: 0.8rem;
  color: rgba(248, 113, 113, 0.9);
}

.expression-tab-panels {
  display: flex;
  flex: 1;
  min-height: 0;
  position: relative;
  grid-column: 3;
  grid-row: 1;
}

.expression-tab-panel {
  display: flex;
  flex-direction: column;
  gap: 12px;
  flex: 1;
  min-height: 0;
  height: 100%;
}

.expression-tab-panel[hidden] {
  display: none;
}

.status {
  margin: 0;
  padding: 12px 14px;
  border-radius: 12px;
  font-size: 0.9rem;
  line-height: 1.45;
  background: rgba(15, 23, 42, 0.7);
  border: 1px solid transparent;
  max-height: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}

.status-error {
  border-color: rgba(248, 113, 113, 0.4);
  background: rgba(248, 113, 113, 0.12);
  color: rgb(248, 113, 113);
}

.status-warning {
  border-color: rgba(251, 191, 36, 0.4);
  background: rgba(251, 191, 36, 0.12);
  color: rgb(251, 191, 36);
}

.status-info {
  border-color: rgba(125, 211, 252, 0.4);
  background: rgba(125, 211, 252, 0.12);
  color: rgb(125, 211, 252);
  list-style: disc;
  padding-left: 28px;
}

.status-success {
  border-color: rgba(74, 222, 128, 0.4);
  background: rgba(74, 222, 128, 0.12);
  color: rgb(74, 222, 128);
}

.splitter {
  width: 8px;
  cursor: col-resize;
  background: linear-gradient(180deg, rgba(148, 163, 184, 0.45), rgba(148, 163, 184, 0.1));
  position: relative;
  transition: background 120ms ease;
  touch-action: none;
}

.splitter::after {
  content: '';
  position: absolute;
  inset: 18px 2px;
  border-radius: 999px;
  background: rgba(226, 232, 240, 0.3);
}

.splitter:focus-visible {
  outline: 2px solid #38bdf8;
  outline-offset: 2px;
}

.splitter:hover,
.splitter-dragging {
  background: linear-gradient(180deg, rgba(59, 130, 246, 0.85), rgba(59, 130, 246, 0.3));
}

.splitter-dragging::after {
  background: rgba(226, 232, 240, 0.6);
}

@media (max-width: 960px) {
  .app {
    flex-direction: column;
  }

  .panel-left,
  .panel-right {
    width: 100% !important;
  }

  .splitter {
    height: 12px;
    width: 100%;
    cursor: row-resize;
    background: linear-gradient(90deg, rgba(148, 163, 184, 0.45), rgba(148, 163, 184, 0.1));
  }

  .splitter::after {
    inset: 2px 18px;
  }
}
